import { makeStyles } from "@material-ui/core";

export const styles = makeStyles((t) => ({
    pageContainer: {
        padding: `0px ${t.spacing(17)}px`,
        margin: '100px 0px'
    },
    urlLink: {
        margin: '0px 5px',
    },
    sp_btwn: {
        display: "flex",
        justifyContent: 'space-between',
        alignItems: "center"
    },
    categoryNameContainer: {
        display: "flex",
        flexWrap: "wrap"
    },
    categoryName: {
        color: "#79AAE7;",
        fontSize: '16px',
        marginBottom: "20px"
    },
    ownShirt: {
        background: "#F2F4F8",
        padding: "20px 19px",
        borderRadius: '20px',
        height: "200px",
        position: 'relative',
        '& img': {
            position: "absolute",
            bottom: 0,
            right: "0px"
        }
    }
}))