import React from 'react'
import TShirt from "assets/icons/shop/TShirt.png";
import { styles } from './shirt.style';

interface ShirtArrayType {
    id: number;
    header: string;
    color: Array<string>;
    price: number;
    styleCount: number;
}

interface ShirtProps {
    shirts: Array<ShirtArrayType>
}

export const Shirt: React.FC<ShirtProps> = ({ shirts }) => {
    const classes = styles();
    return (
        <>
            {
                // I duplicated it for make it more :) ;)   XD XD XD
                [...shirts, ...shirts].slice(0, 3).map(({ id, header, color, price, styleCount }) => (
                    <div className={classes.container} key={id}>
                        <img src={TShirt} alt="" />
                        <p className={classes.header} >{header}</p>
                        <p className={classes.header} >${price}.00 USD</p>
                        <div style={{ display: 'flex', marginBottom: "5px" }}>
                            {
                                color.map((clr) => (
                                    <div style={{ width: "20px", height: "20px", background: clr, borderRadius: '50%', marginRight: "5px" }}></div>
                                ))
                            }
                        </div>
                        <p style={{ color: "#5E82A6", fontSize: "12px" }}>Available in {styleCount} Style</p>
                    </div>
                ))
            }
        </>
    )
}