import { Grid, Typography } from '@material-ui/core';
import ownShirt from "assets/icons/shop/ownShirt.png";
import { ReactComponent as Search } from "assets/icons/shop/search.svg";
import { BonfireField } from 'components/BonfireField/BonfireField';
import Footer from 'components/Footer';
import Header from 'components/Header';
import { Form, Formik } from 'formik';
import React from 'react';
import { Category } from './components';
import { categories, links } from './fakeData';
import { styles } from './shop.style';

export const Shop = () => {
    const classes = styles();
    return (
        <>
            <Header />
            <Grid container className={classes.pageContainer}>
                <Grid item xs={12}>
                    <p><span className={classes.urlLink}>Home</span>/<span className={classes.urlLink}>Shop</span></p>
                </Grid>
                <div style={{ height: "280px" }} className={classes.sp_btwn}>
                    <div style={{ height: "100%", width: "100%" }}>
                        <h1 className="heading-1" style={{ marginBottom: "0px" }}>Explore the Style Factory marketplace</h1>
                        <div>
                            <Typography style={{ fontSize: '17px', margin: "30px 0px" }}>BY CATEGORY</Typography>
                            <Grid container className={classes.categoryNameContainer}>
                                {
                                    links.map((link) => (
                                        <Grid item lg={3} className={classes.categoryName} key={link.id}>{link.name}</Grid>
                                    ))
                                }
                            </Grid>
                        </div>
                    </div>
                    <div style={{ height: "100%", display: 'flex', flexDirection: 'column', justifyContent: 'space-between', }} >
                        <Formik
                            initialValues={{}}
                            onSubmit={() => { }}
                        >
                            {({ }): React.ReactNode => {
                                return (
                                    <Form>
                                        <Grid item xs={12}>
                                            <BonfireField
                                                style={{ width: "346px" }}
                                                name="apiName"
                                                placeholder="Search campaigns..."
                                                inputIcon={<Search />}
                                            />
                                        </Grid>
                                    </Form>
                                )
                            }}
                        </Formik>
                        <div className={classes.ownShirt}>
                            <h1 className="heading-2" style={{ marginBottom: "0px", paddingRight: "30px" }}>Want to design your own shirt?</h1>
                            <a
                                href="#"
                                className="btn-primary header__get-started-btn"
                                style={{ background: "white", color: "black", marginTop: "25px", fontSize: '12px' }}
                            >
                                get started
                                </a>
                            <img src={ownShirt} />
                        </div>
                    </div>
                </div>
                <Grid container>
                    <Category categories={categories} />
                </Grid>
            </Grid>
            <Footer />
        </>
    )
}